<template>
  <div>
    <v-warning></v-warning>
    <div class="monitsearch">
      <!-- 	<el-button type="primary" @click="sendbar = true">{{$t('monitoring.sendmonitoring')}}</el-button>
			<el-button type="primary" @click="sendbar = false">{{$t('monitoring.receivemonitoring')}}</el-button> -->
      <div class="monitku">{{$t('monitoring.monitoring')}}</div>
      <div class="operation_select input_wrap">

        <el-select value-key="id" v-model="monitoring_id">
          <el-option v-for="item in monitoringList" :key="item.monitoring_id" :label="item.monitoring_name" :value="item.monitoring_id">
          </el-option>
        </el-select>
      </div>

      <!-- 库位 -->
      <div class="monitku">{{$t('storage.category_name')}}</div>
      <div class="operation_select input_wrap">

        <el-select value-key="id" v-model="warehouse_category_id" @change="categoryTypeChange">
          <el-option v-for="item in categoryTypeList" :key="item.id" :label="item.category_name" :value="item.id">
          </el-option>
        </el-select>
      </div>
      <div class="monitku">{{$t('storage.warehouse_name')}}</div>
      <div class="operation_select input_wrap">

        <el-select value-key="id" v-model="warehouse_name_id">
          <el-option v-for="item in warehouseList" :key="item.id" :label="item.warehouse_name" :value="item.id">
          </el-option>
        </el-select>
      </div>
      <div class="monitku">
        <el-button type="primary" @click="monitoringSearch">{{$t('common.search')}}</el-button>
      </div>

    </div>

    <!-- 发出看板 -->
    <div>
      <!-- 			<div class="monittitle">{{$t('monitoring.title')}}</div> -->
      <template>
        <el-table :header-cell-style="{background:'#DFEBF9',color:'#606266'}" :loading="loading" :data="monitoringData"
          :empty-text="$t('common.nodata')" border>
          <el-table-column type="index" width="50" :label="$t('common.No')">
          </el-table-column>
          <el-table-column width="32" prop="color">
            <template slot-scope="scope">
              <div><span v-if="scope.row.color==1" class="lightcolor light_one"></span></div>
              <div><span v-if="scope.row.color==2" class="lightcolor light_two"></span></div>
              <div><span v-if="scope.row.color==3" class="lightcolor light_three"></span></div>
              <div><span v-if="scope.row.color==4" class="lightcolor light_four"></span></div>
              <div><span v-if="scope.row.color==5" class="lightcolor light_five"></span></div>
              <div><span v-if="scope.row.color==6" class="lightcolor light_six"></span></div>
            </template>
          </el-table-column>
          <el-table-column prop="shelf_id" :label="$t('storage.shelf_id')">
          </el-table-column>
          <el-table-column prop="order_id" :show-overflow-tooltip='true' :label="$t('storage.order_code')">
          </el-table-column>
          <el-table-column prop="category_name" :label="$t('storage.category_name')">
          </el-table-column>
          <el-table-column prop="warehouse_name" :label="$t('storage.warehouse_name')">
          </el-table-column>
          <el-table-column prop="part_num" :label="$t('storage.part_num')">
          </el-table-column>
          <el-table-column prop="label_code" :label="$t('storage.label_code')">
          </el-table-column>
          <el-table-column :show-overflow-tooltip='true' prop="position" :label="$t('storage.position')">
          </el-table-column>
        </el-table>
      </template>
    </div>
  </div>
</template>
<script>
  import {
    searchWarehouseCategory,
    searchWarehouseNameByCategory
  } from '@/request/api/materialshelf'
  import {
    lightedMate,
    warehouseTransferList
  } from '@/request/api/material'
  import VWarning from './warningshelf'
  export default {
    components: {
      VWarning
    },
    data() {
      return {
        loading: false,
        storage_location: 0,
        categoryTypeList: [{
          'category_name': '所有',
          'id': 0
        }],
        warehouseList: [{
          'warehouse_name': '所有',
          'id': 0
        }],
        warehouse_category_id: 0,
        warehouse_name_id: 0,
        monitoringData: [],
        receivelist: [],
        monitoring_id: 1,
        monitoringList: [{
          monitoring_id: 1,
          monitoring_name: '接收信息看板'
        }, {
          monitoring_id: 2,
          monitoring_name: '发出信息看板'
        }],
        timer1: null,
        timer2: null

      }
    },
    watch: {
      monitoring_id(val) {
        if (val == 1) {
          clearInterval(this.timer2);
          this.getTransList()
          this.timer1 = window.setInterval(() => {
            setTimeout(this.getTransList, 0)
          }, 5000)
        } else {
          clearInterval(this.timer1);
          this.getSendList()
          this.timer2 = window.setInterval(() => {
            setTimeout(this.getSendList, 0)
          }, 5000)
        }
      }
    },
    mounted() {
      // this.getSendList()
      this.getTransList()
      // this.getCategoryType()
      //轮询列表
      this.timer1 = window.setInterval(() => {
        // setTimeout(this.getSendList, 0)
        setTimeout(this.getTransList, 0)
      }, 5000)
    },
    beforeDestroy() {
      if (this.timer1) { //如果定时器还在运行 或者直接关闭，不用判断
        clearInterval(this.timer1); //关闭
      }
      if (this.timer2) { //如果定时器还在运行 或者直接关闭，不用判断
        clearInterval(this.timer2); //关闭
      }
    },
    methods: {
      getSendList() {
        lightedMate({
          warehouse_category_id: this.warehouse_category_id,
          warehouse_name_id: this.warehouse_name_id
        }).then(res => {
          if (res.result) {
            this.monitoringData = res.mate_list
          } else {
            this.$message({
              showClose: true,
              message: res.msg,
              type: 'error'
            })
          }
        })
      },
      getTransList() {
        warehouseTransferList({
          warehouse_category_id: this.warehouse_category_id,
          warehouse_name_id: this.warehouse_name_id
        }).then(res => {
          if (res.result) {
            this.monitoringData = res.data
          } else {
            this.$message({
              showClose: true,
              message: res.msg,
              type: 'error'
            })
          }
        })
      },
      // 获取仓库类型
      getCategoryType() {
        searchWarehouseCategory({}).then(res => {
          if (res.result) {
            this.categoryTypeList = this.categoryTypeList.concat(res.data_list)
          }
        })
      },
      // 获取库位
      getWarehouseList() {
        this.warehouseList = [{
          'warehouse_name': '所有',
          'id': 0
        }]
        this.warehouse_name_id = 0
        searchWarehouseNameByCategory({
          warehouse_category_id: this.warehouse_category_id
        }).then(res => {
          if (res.result) {
            this.warehouseList = this.warehouseList.concat(res.data)
          }
        })
      },
      categoryTypeChange() {
        this.warehouse_name_id = 0
        this.getWarehouseList()
      },
      monitoringSearch() {
        // window.location.reload()
      }
    }
  }
</script>
<style>
  .monitsearch {
    display: flex;
    font-size: 14px;
    margin-bottom: 10px;
    align-items: center;
  }

  .monitsearch .el-select {
    width: 160px;
  }

  .monitsearch .el-button {
    margin-right: 20px;
    margin-bottom: 20px;
    height: 40px;
  }

  .monitku {
    height: 40px;
    line-height: 40px;
    margin: 0 20px;
  }
</style>



