<template>
  <div>


    <el-scrollbar style='max-height:120px;overflow-x: hidden'>
      <div class="warning_div">
        <span class="warning_span_tips">{{$t('monitoring.offlinetips')}}</span>
        <span v-for="item in warninglist" class="warning_span">
          {{item}}
        </span>
      </div>
    </el-scrollbar>

  </div>
</template>
<script>
  import {
    offlineShelfList
  } from '@/request/api/materialshelf'
  export default {
    data() {
      return {
        warninglist: [],
      }
    },
    mounted() {
      this.getOffline()
    },
    methods: {
      getOffline() {
        offlineShelfList({}).then(res => {
          if (res.result) {
            this.warninglist = res.data
          } else {
            this.$message({
              showClose: true,
              message: res.msg,
              type: 'error'
            })
          }
        })
      }
    }
  }
</script>
<style scope>
  .warning_div {
    display: flex;
    margin: 20px;
    padding: 10px;
    border: 1px solid red;
    border-radius: 2px;
    background: #fff1f0;
    max-height: 120px;
    flex-wrap: wrap;
  }
  .warning_span_tips {
    font-weight: bold;
    line-height: 30px;
  }
  .warning_span {
    font-size: 12px;
    background: red;
    border-radius: 4px;
    color: white;
    padding: 4px 8px;
    margin: 4px;
    display: block;
  }
</style>



